html {
  font-size: 62.5%;
}

body {
  font-family: "Kanit", sans-serif;
  color: #ffff;
  font-weight: 300;
  line-height: 1.6;
}

.container {
  width: 114rem;
  max-width: 100%;
  margin: 0 auto;
}

h1 {
  text-transform: uppercase;
}

header {
  background-color: #07041c;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 7.4rem;
}
.header__logo {
  flex-grow: 1;
  width: 6.1rem;
  margin-right: 1.92rem;
}
.header__nav {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-grow: 2;
}
.header__nav li {
  list-style: none;
  font-weight: 400;
  font-size: 1.6rem;
  margin-right: 5rem;
}
.header__nav li a {
  color: #ffff;
  text-decoration: none;
}
.header__nav li:first-child {
  margin-right: 3rem;
  font-weight: 700;
  border: 1px solid #fff;
  padding: 1rem 2.5rem;
  font-size: 1.5rem;
  font-weight: 500;
  text-transform: uppercase;
  border-radius: 5rem;
  line-height: 2.5rem;
}
.header__nav li:last-child {
  margin-right: 0;
  margin-left: -1rem;
}

main {
  width: 100%;
  height: 101.6rem;
  margin: 0 auto;
  background: radial-gradient(65.51% 65.51% at 50% 19.09%, #221c3f 0%, #07041c 100%);
  padding-top: 0.1rem;
}

.showcase {
  margin-top: 5.1rem;
  background: linear-gradient(180deg, #14102b 0%, #282349 100%);
  height: 38.4rem;
  border-radius: 0.8rem;
  display: grid;
  grid-template-areas: "content svg" "content svg";
  grid-template-columns: repeat(2, 1fr);
}
.showcase__left {
  grid-area: content;
  width: 56.5rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 1.9rem 6rem;
}
.showcase__title {
  font-weight: 800;
  font-size: 4rem;
  line-height: 6rem;
}
.showcase__cta {
  text-transform: uppercase;
  padding: 0.9rem 3.9rem !important;
  font-size: 1.6rem !important;
}
.showcase__content {
  margin-top: -1rem;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 2.8rem;
  letter-spacing: 0.5px;
}
.showcase__logos {
  margin-top: 3.6rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.showcase__logos a {
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 4rem;
  margin-right: 2.1rem;
  height: 2.5rem;
}
.showcase__logos a:nth-child(1) {
  width: 7rem;
}
.showcase__logos a:nth-child(1) img {
  width: 40px;
  transform: translate(42%, 50%);
}
.showcase__logos a:nth-child(2) {
  width: 11.4rem;
}
.showcase__logos a:nth-child(2) img {
  width: 10rem;
  transform: translate(9%, 32%);
}
.showcase__logos a:nth-child(3) {
  width: 6.8rem;
}
.showcase__logos a:nth-child(3) img {
  width: 5rem;
  transform: translate(18%, 22%);
}
.showcase__right {
  grid-area: svg;
}
.showcase__right img {
  margin-top: -3.2rem;
  margin-right: 1.7rem;
  width: 46rem;
}

.linear-button {
  position: relative;
  padding: 1.2rem 2.75rem;
  display: inline-block;
  font-size: 1.5rem;
  font-weight: 700;
  background: linear-gradient(237.12deg, #ffcea8 0%, #c3a6fc 50.52%, #80fef7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.linear-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50px;
  padding: 1.7px;
  background: linear-gradient(237.12deg, #ffcea8 0%, #c3a6fc 50.52%, #80fef7 100%);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}